/* keyframes */
@keyframes aniBounce {
	0% {
		transform: translate3d(0, 0, 0);
	}
	50% {
		transform: translate3d(0, -10%, 0);
	}
	100% {
		transform: translate3d(0, 0, 0);
	}
}
@keyframes stars {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes flash {
	from, 50%, to {
		opacity: 1;
	}
	25%, 75% {
		opacity: 0;
	}
}
@keyframes sniping {
	0% {
		opacity: 0;
	}
	10% {
		opacity: 0.5;
		transform: scale(3);
		transition: all 0.3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}
@keyframes stamp {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	50% {
		opacity: 0.5;
		transform: scale(2);
	}
	51% {
		opacity: 1;
		transform: scale(0.5);
		transition: scale 0.3s ease-in-out;
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}
@keyframes bemin {
	0% {
		opacity: 0;
		transform: translate3d(50%, 50%, 0) scale(0.5) rotate(45deg);
		left: 30%;
		top: 300px;
	}
	50% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
		left: -5%;
		top: -71px;
	}
	100% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
		left: 0;
		top: 0;
	}
}
@keyframes eats {
	0% {
		opacity: 0;
		transform: translate3d(50%, 50%, 0) scale(0.5) rotate(-45deg);
		right: 35%;
		top: 300px;
	}
	50% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
		right: 10%;
		top: 200px;
	}
	100% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
		right: 11%;
		top: 248px;
	}
}
@keyframes yogiyo {
	0% {
		opacity: 0;
		transform: translate3d(50%, 50%, 0) scale(0.5) rotate(-90deg);
		right: 30%;
		top: 300px;
	}
	50% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
		right: -3%;
		top: -20px;
	}
	100% {
		opacity: 1;
		transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
		right: 0;
		top: 34px;
	}
}