@charset "UTF-8";

@import "base";

//motion
[data-aos][data-aos][data-aos-duration], body[data-aos-duration] [data-aos] {transition-duration: 1s;}
[data-aos="fade-left"] { transform: translate3d(20%,0,0); }
[data-aos="fade-right"] { transform: translate3d(-20%,0,0); }
[data-aos="fade-up"] { transform: translate3d(0,20%,0); }
[data-aos="fade-down"] { transform: translate3d(0,-20%,0); }

[data-aos="flash"]{animation: flash 1s infinite;}
[data-aos="highlight"].aos-animate:before {width: calc(100% + 10px);}
[data-aos] [data-aos]{transition-delay: .6s;}

.stamp[data-aos="zoom-in"]{transform: scale(.2); transition-duration: .4s;}
[data-aos="fade-down"]{transform: translate3d(0,-100%,0);}
[data-aos="sniping"] img{opacity: 0; animation-duration: .5s; animation-fill-mode:forwards; transform-origin: center;}
[data-aos="sniping"].aos-animate img{animation-name: sniping;}

.aos-animate .bemin { animation-name: bemin; animation-delay: 0.2s;}
.aos-animate .eats { animation-name: eats; animation-delay: 0.4s;}
.aos-animate .yogiyo { animation-name: yogiyo; animation-delay: 0.3s;}

.bounce li{animation: aniBounce 1s infinite alternate ease-in; animation-fill-mode: both;}

.section08 [data-aos="highlight"].aos-animate .highlight:before{width: calc(100% + 10px); transition: width .4s;}
.section08 [data-aos="highlight"].aos-animate p:nth-child(2) .highlight:before{transition-delay: .2s;}

.section12 .thumb_con li{transform: translate3d(0,200px,0); opacity: 0; transition: none;}
.section12 .thumb_con.aos-animate li{opacity: 1; transform: translateZ(0); transition-property: opacity,transform; transition-duration: .4s;}

.section12 .thumb_con li:nth-child(1) { transition-delay: 0.1s; }
.section12 .thumb_con li:nth-child(2) { transition-delay: 0.2s; }
.section12 .thumb_con li:nth-child(3) { transition-delay: 0.3s; }
.section12 .thumb_con li:nth-child(4) { transition-delay: 0.4s; }
.section12 .thumb_con li:nth-child(5) { transition-delay: 0.5s; }
.section12 .thumb_con li:nth-child(6) { transition-delay: 0.6s; }

.section13 .info_con .ranking_list li:nth-child(1) { transition-delay: 0.1s; }
.section13 .info_con .ranking_list li:nth-child(2) { transition-delay: 0.2s; }
.section13 .info_con .ranking_list li:nth-child(3) { transition-delay: 0.3s; }
.section13 .info_con .ranking_list li:nth-child(4) { transition-delay: 0.4s; }
.section13 .info_con .ranking_list li:nth-child(5) { transition-delay: 0.5s; }
.section13 .info_con .ranking_list li:nth-child(6) { transition-delay: 0.6s; }

.section16 .process_list .item{transform: translate3d(0,200px,0); opacity: 0; transition-property: opacity,transform; transition-duration: .4s;}
.section16 .process_list .item:nth-child(1) { transition-delay: 0.1s; }
.section16 .process_list .item:nth-child(2) { transition-delay: 0.2s; }
.section16 .process_list .item:nth-child(3) { transition-delay: 0.3s; }
.section16 .process_list .item:nth-child(4) { transition-delay: 0.4s; }
.section16 .process_list .item:nth-child(5) { transition-delay: 0.5s; }
.section16 .process_list .item:nth-child(6) { transition-delay: 0.6s; }
.section16 .process_list .item:nth-child(7) { transition-delay: 0.7s; }
.section16 .process_list .item:nth-child(8) { transition-delay: 0.8s; }
.section16 .process_list .item:nth-child(9) { transition-delay: 0.9s; }

.section16 .process_list.aos-animate .item:nth-child(1),
.section16 .process_list.aos-animate .item:nth-child(2),
.section16 .process_list.aos-animate .item:nth-child(3),
.section16 .process_list.aos-animate .item:nth-child(4),
.section16 .process_list.aos-animate .item:nth-child(5),
.section16 .process_list.aos-animate .item:nth-child(6),
.section16 .process_list.aos-animate .item:nth-child(7),
.section16 .process_list.aos-animate .item:nth-child(8),
.section16 .process_list.aos-animate .item:nth-child(9) { opacity: 1; transform: translateZ(0); }

.section17 .table_con li:nth-child(1) .stamp{transition-duration: 0.4s; transition-delay: 0.2s;}
.section17 .table_con li:nth-child(2) .stamp{transition-duration: 0.4s; transition-delay: 0.3s;}
.section17 .table_con li:nth-child(4) .stamp{transition-duration: 0.4s; transition-delay: 0.4s;}
.section17 .table_con li:nth-child(9) .stamp{transition-duration: 0.4s; transition-delay: 0.5s;}


@media all and (max-width:960px){
    .section13 .info_con .delivery_logo .bound-ani{animation: none; opacity: 1;}
}
@media all and (max-width:768px){
    .section01 .info .flex_con .icon_con .blod [data-aos="flash"]{animation: none;}
    .section01 .info .flex_con .txt_con[data-aos]{transform: translate3d(0,0,0); opacity: 1;}
    .section03 .receipt .stamp[data-aos="zoom-in"]{transition-delay: 0s;}
    .section07 .icon_list li img{animation: none; opacity: 1;}
    .section08 [data-aos="highlight"].aos-animate .highlight:before{width: calc(100% + 1.30vw);}
    .section10 .img_box .circle_box li{transform: translate3d(0,0,0); opacity: 1;}
    .section15 .banner .stamp[data-aos="zoom-in"]{opacity: 1;}
}

/* keyframes */
@keyframes aniBounce{
    0% { transform: translate3d(0, 0, 0); }
    50% { transform: translate3d(0, -10%, 0); }
    100% { transform: translate3d(0, 0, 0); }
}

@keyframes stars {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes flash {
    from, 50%, to {opacity: 1;}
    25%, 75% {opacity: 0;}
}
@keyframes sniping {
    0%{
        opacity: 0;
    }
    10%{
        opacity:.50;
        transform: scale(3);
        transition: all .3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    }
    100%{
        opacity:1;
        transform: scale(1);
    }
}
@keyframes stamp {
    0%{
        opacity: 0;
        transform: scale(0);
    }
    50%{
        opacity: 0.5;
        transform: scale(2);
    }
    51%{
        opacity: 1;
        transform: scale(0.5);
        transition: scale .3s ease-in-out;
    }
    100%{
        opacity:1;
        transform: scale(1);
    }
}
@keyframes bemin {
    0% {
        opacity: 0;
        transform: translate3d(50%,50%, 0) scale(0.5) rotate(45deg);
        left: 30%; top: 300px;
    }
    50%{
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
        left: -5%; top: -71px;
    }
    100% {
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
        left: 0; top: 0;
    }
}
@keyframes eats {
    0% {
        opacity: 0;
        transform: translate3d(50%,50%, 0) scale(0.5) rotate(-45deg);
        right: 35%; top: 300px;
    }
    50%{
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
        right: 10%; top: 200px;
    }
    100% {
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
        right: 11%; top: 248px;
    }
}
@keyframes yogiyo {
    0% {
        opacity: 0;
        transform: translate3d(50%,50%, 0) scale(0.5) rotate(-90deg);
        right: 30%; top: 300px;
    }
    50%{
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1.2) rotate(0deg);
        right: -3%; top: -20px;
    }
    100% {
        opacity: 1;
        transform: translate3d(0%, 0%, 0) scale(1) rotate(0deg);
        right: 0; top: 34px;
    }
}
